<template>
  <div class="feedbox">
    <div class="content">
      <textarea placeholder="请填写您要反馈的内容" v-model="content" rows="5" />
    </div>
    <div class="submit-btn">提交反馈</div>
    <div class="history">
      <p>反馈记录</p>
      <p>{{ historyInfo ? historyInfo : "暂无内容" }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: '',
      historyInfo: ''
    };
  }
};
</script>

<style lang="less" scoped>
.feedbox {
  padding: 19px 25px 25px 21px;
  box-sizing: border-box;
  .content {
    height: 362px;
    width: 100%;
    overflow: hidden;
    background: #ffffff;
    textarea {
      width: 625px;
      font-size: 34px;
      padding: 37px 36px;
      border: none;
    }
    textarea ::placeholder {
      color: #bfbfbf;
    }
  }
  .submit-btn {
    width: 704px;
    height: 90px;
    background-color: #3198ed;
    box-shadow: 0px 5px 5px 0px rgba(89, 166, 250, 0.1);
    border-radius: 45px;
    margin: 36px auto;
    color: #ffffff;
    text-align: center;
    line-height: 90px;
    font-size: 36px;
    letter-spacing: 7px;
  }
  .history {
    p:first-child {
      color: #888888;
      font-size: 30px;
    }
    p:last-child {
      color: #333333;
      font-size: 34px;
    }
  }
}
</style>



